import firebase from "firebase/app";
import "firebase/messaging";
import "firebase/firestore";
import { firebase as firebaseConfig, firebaseWebPushCertificate } from "config";


let firebaseMessaging = null;
let firebaseFireStore = null;
let initializedFirebaseApp = null;
if(firebase.messaging.isSupported()) {
	initializedFirebaseApp = firebase.initializeApp(firebaseConfig);
	firebaseMessaging = initializedFirebaseApp.messaging();
	firebaseMessaging.usePublicVapidKey(firebaseWebPushCertificate);
	firebaseFireStore = initializedFirebaseApp.firestore();
}

export const messaging = firebaseMessaging;
export const firestore = firebaseFireStore;






export const getFirestoreDoc = (collection, doc) => {
	return new Promise((resolve, reject) => {
		if (!!firestore) {
			firestore.collection(collection).doc(doc).get().then(async (querySnapshot) => {
				resolve(querySnapshot.data());
			}).catch(err => {
				reject(err);
			});
		}
		else {
			reject("Firestore Unsupported");
		}
		
		
	});
}


export const createUpdateFirestoreDoc = (collection, doc, data) => {
	return new Promise((resolve, reject) => {
		if (!!firestore) {
			firestore.collection(collection).doc(doc).get().then(async (querySnapshot) => {
				let querySnapshotData = querySnapshot.data();
				let newData = {...querySnapshotData, ...data};
				firestore.collection(collection).doc(doc).set(newData);
				resolve(newData);
			}).catch(err => {
				reject(err);
			});	
		}
		else {
			reject("Firestore Unsupported");
		}
		
	});
}


export default firebase;